    background-color: #a77c00;
  }

  .hamburgericon {
    color: #fff;
    font-size: 25px;
  }

  .homecategorygrid {
    -ms-grid-columns: 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .home_product_list_grid {
    -ms-grid-columns: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 767px) {
  .homecategorygrid {
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }

  .home_product_list_grid {
    -ms-grid-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 479px) {
  .homecategorygrid {
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }

  .home_product_list_grid {
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }
}

.home_screen_big_grid {
  height: 40em;
  -ms-grid-columns: 1.5fr 1fr;
  grid-template-columns: 1.5fr 1fr;
  -ms-grid-rows: auto;
  grid-template-rows: auto;
  background-color: #2a1b1b;
}

.homescreen_big_img_column {
  background-image: url(/static/media/shoe.f660110f.png);
  background-position: 50% 50%;
  background-size: cover;
}

.homescreen_big_image_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homescreen_fixed_bg {
  height: 30em;
  background-image: url(/static/media/p2.01d1fdcb.jpg);
  background-position: 50% 50%;
  background-size: cover;
  background-attachment: fixed;
}

.money_back_grid {
  height: 30em;
  -ms-grid-rows: auto;
  grid-template-rows: auto;
  background-color: #0f0f07;
}

.money_back_text_column {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/static/media/w.76a2a0d4.png);
  background-position: 50% 50%;
  background-size: cover;
  transition: transform 200ms ease;
  cursor: pointer;
}

.money_back_text_column:hover {
  -webkit-filter: sepia(100%);
  filter: sepia(100%);
}

.money_back_img {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  background-image: url(/static/media/money-p-1080.a1992ed8.png);
  background-position: 50% 50%;
  background-size: cover;
}

.heading-2 {
  margin-top: 10px;
  margin-bottom: 20px;
  margin-left: 20px;
  color: #c7ddd8;
  text-align: center;
}

.big_img_text {
  color: #dadada;
  font-size: 90px;
}

.big_img_desc {
  margin-top: 20px;
  padding-right: 10px;
  padding-left: 10px;
  color: #e2cfcf;
  font-size: 20px;
}

.big_img_button {
  width: 30%;
  margin-top: 20px;
  border-style: solid;
  border-width: 3px;
  border-color: #618522;
  border-radius: 20px;
  background-color: transparent;
  transition: background-color 200ms ease;
  color: #ebcccc;
  font-size: 30px;
  text-align: center;
}

.big_img_button:hover {
  background-color: #a58b18;
  transform: translate(0px, 0px) translate(0px, 0px);
}

@media screen and (max-width: 991px) {
  .heading-2 {
    color: #e3e9e7;
    font-size: 20px;
    line-height: 20px;
  }

  .big_img_text {
    font-size: 60px;
  }

  .big_img_desc {
    padding-right: 5px;
    padding-left: 5px;
    color: #f1e2e2;
  }

  .big_img_button {
    width: 80%;
  }
}

@media screen and (max-width: 767px) {
  .big_img_desc {
    font-size: 15px;
  }
}

@media screen and (max-width: 479px) {
  .home_screen_big_grid {
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }

  .money_back_grid {
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
  }

  .money_back_img {
    align-items: flex-start;
  }

  .heading-2 {
    margin-top: 10px;
    color: #76948c;
    font-size: 15px;
  }

  .big_img_desc {
    padding-left: 10px;
  }
}

